* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  background-color: #f8f2de;
  font-family: Roboto, sans-serif;
}

main {
  flex-direction: row;
  display: flex;
}

h1 {
  color: tomato;
}

header {
  height: 15vh;
  border: 1px solid;
  place-items: center;
  display: grid;
}

.image {
  background-color: #e0ffff;
  margin: 2vw;
}

img {
  margin: 10vw;
  padding: 2vw;
}

.box1 {
  grid-template-rows: 1fr 1fr 1fr 1fr;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  display: grid;
}

h2 {
  grid-column: 1 / 3;
  place-items: center;
  font-size: 2em;
  display: grid;
}

.box1 > p {
  grid-column: 2 / 5;
  place-items: center;
  padding: 1vw;
  font-size: 2em;
  display: grid;
}

.block {
  background-color: plum;
  border: 2px solid #d3d3d3;
  grid-area: 3 / 3 / 5 / 4;
  place-items: center;
  margin: .5vw;
  display: grid;
}

